import {
  Box,
  Heading,
  Text,
  Button,
  Divider,
  VStack,
} from "@chakra-ui/react";
import { Container } from "@chakra-ui/react";

const LocationSection = () => {
  return (
    <Container maxW="4xl" my={16}>
      <VStack spacing={6} textAlign="center">
        <Heading as="h2" size="lg" color="pink.600">
           
        </Heading>
        <Text fontSize="xl" fontWeight="medium">
            
        </Text>
        <Text>.  , . , . 10</Text>
        <Text> : 09:50</Text>
        <Button
          as="a"
          href="https://yandex.ru/maps/?text=.+,+.+10,++"
          target="_blank"
          rel="noopener noreferrer"
          colorScheme="pink"
          size="lg"
          borderRadius="full"
          px={8}
          shadow="md"
          _hover={{ transform: "scale(1.05)" }}
          transition="transform 0.2s"
        >
           
        </Button>

        <Divider my={6} />

        <Heading as="h2" size="lg" color="pink.600">
           
        </Heading>
        <Text>...  , . , . , . 62</Text>
        <Button
          as="a"
          href="https://yandex.ru/maps/?text=.+,+.+,+.+62,++"
          target="_blank"
          rel="noopener noreferrer"
          colorScheme="pink"
          size="lg"
          borderRadius="full"
          px={8}
          shadow="md"
          _hover={{ transform: "scale(1.05)" }}
          transition="transform 0.2s"
        >
           
        </Button>
      </VStack>
    </Container>
  );
};

export default LocationSection;